.aboutLayout {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 0;
  gap: 0 var(--padding-md);
  height: 100%;
  & .boxContainer {
    display: grid;
    gap: var(--padding-md);
    height: inherit;
    width: 100%;
    grid-template-columns: repeat(3, minmax(33%, auto));
    grid-template-rows: repeat(2, 1fr);
  }
}
